header {
	background: $blue;
	padding: 37px 0 27px 0;
	& > .wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
        position: relative;
        @media (max-width:700px) {
            display: block;
        }
	}
    a.btn.anim_tree:hover span {
        color: #fff !important;
    }
	a {
		color: #fff;
		&.all {
			color: $yellow;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
		& + a {
			margin-right: 20px;
		}
	}
}
.header {
	&_logo {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		&_img {
			margin-right: 30px;
		}
		&_text {
			text-transform: uppercase;
			font-size: 28px;
			display: block;
			margin-bottom: 16px;
			&:hover {
				text-decoration: none;
			}
            @media (max-width:930px) {
                font-size: 24px;
            }
            @media (max-width:540px) {
                font-size: 20px;
            }
            
		}
        @media (max-width:540px) {
            &_block {
                a {
                    display: block;
                }
            }
        }
	}
	&_btn {
        @media (max-width:700px) {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 20px;
        }
		a {
			margin: 0;
            @media (max-width:700px) {
                margin: 0 15px;   
            }
            @media (max-width:540px) {
                margin: 0 10px;  
                padding: 15px 0!important;
                width: 48%;
            }
			& + a {
				margin-left: 17px;
                @media (max-width:860px) {
                    margin-left: 0;
                    margin-top: 10px;
                }
                @media (max-width:700px) {
                    margin-top: 0;
                }
			}
            @media (max-width:1024px) {
                padding: 18px 16px;
                font-size: 11px;
            }
            @media (max-width:860px) {
                display: block;   
            }
		}
        
	}
}
nav {
	background: $dark;
	a {
		font-size: 16px;
		color: #fff;
		padding: 25px 0 22px 0;
		border-bottom: 3px solid rgba(255,255,255,0);
		display: inline-block;
		&.current,
		&:hover {
			text-decoration: none;
			border-color: $yellow;
		}
        @media (max-width:900px) {
            &.nav_a {
               border-bottom: none;
                display: block;
                padding: 10px;
                margin-bottom: 10px;
                &.current,
                &:hover {
                    background: $yellow;
                    border: none;
                    color:  $dark;
                } 
            }
            
            
        }
	}
	.wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	div {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-right: 30px;
		& + a {
			position: relative;
			padding-left: 30px!important;
			white-space: nowrap;
            color: $yellow;
            text-transform: uppercase;
			&:before {
				content: '';
				display: block;
				position: absolute;
				width: 1px;
				height: 50%;
				background: #ababab;
				left: 0;
				top: 50%;
				transform: translate(0,-50%);
			}
			&:hover {
				border-color: rgba(255,255,255,0);
                color: #fff;
			}
		}
	}
}