.slider {
    background: url(../images/bg/main_wh_slider.jpg) no-repeat top center;
    background-size: cover;
    overflow: hidden;
    
    & > .wrapper {
        padding: 0;
    }
	&_partners {
		background: #efefef;
		padding: 40px 0 30px 0;
        img {
            margin: 0 auto;
            filter: grayscale(1) hue-rotate(360deg) invert(0.2);
            &:hover {
                filter: grayscale(0) hue-rotate(0deg) invert(0);
            }
        }
        .owl {
            &-nav {
                font-size: 24px;
                font-family: 'icomoon' !important;
                      speak: none;
                      font-style: normal;
                      font-weight: normal;
                      font-variant: normal;
                      text-transform: none;
                      line-height: 1;
                      -webkit-font-smoothing: antialiased;
                      -moz-osx-font-smoothing: grayscale;
                & > * {
                    position: absolute;
                    top: 50%;
                    margin-top: -15px;
                    color: #b9b9b9;
                    &:hover {
                        
                        &:before {
                            color: $yellow;
                        }
                        
                    }
                }
            }
            &-prev {
                left: -50px;
                &:before {
                    color: #b9b9b9;
                   content: "\e92b";
                    display: block;
                    transform: rotate(-180deg);
                        font-size: 27px;
                }
            }
            &-next {
                    right: -50px;
                &:before {
                    color: #b9b9b9;
                    content: "\e92b";
                    display: block;
                        font-size: 27px;
                }
            }
            &-dots {
                text-align: center;
                position: absolute;
                bottom: 10px;
                left: 0;
                right: 0;
                z-index: 999;
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0 4.5px;
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background: #6a7081!important;
                    transition: all ease 0.4s;
                    &.active {
                        box-shadow: 0 0 0 4px #fab915;
                        background: none!important;
                    }
                }
            }
        }
	}
    &_text {
        padding-left: 10px;
        &_h1 {
            color: #363a45;
            font-size: 40px;
            font-weight: 500;
            line-height: 49px;
            max-width: 600px;
            margin-bottom: 51px;
            margin-left: 18px;
            @media (max-width:920px) {
                font-size: 28px;
                max-width: 400px;
                margin-bottom: 30px;
            }
            @media (max-width:600px) {
                max-width: 100%;
            }
        }
    }
    &_item {
        background-repeat: no-repeat;
        background-position: right bottom;
        padding-top: 150px;
            padding-bottom: 82px;
        @media (max-width:800px) {
            background-size: 80%;
        }
        @media (max-width:600px) {
            padding-top: 40px;
            padding-bottom: 30px;
            background-size: 60%;
        }
        @media (max-width:420px) {
            background: none!important;
            text-align: center;
        }
    }
    &_name {
        font-weight: 100;
        font-size: 20px;
        color: #363a45;
        margin-bottom: 45px;
        margin-left: 18px;
    }
    i {
        &.icon-play-arrow {
            width: 150px;
            height: 150px;
            display: block;
            border-radius: 50%;
            background: $yellow;
            color: #fff;
            font-size: 19px;
            text-align: center;
            padding-top: 56px;
            margin-left: 1px;
            border: 12px solid #fff;
            box-shadow: 0 0 1px 0px #000;
            transition: all ease 0.4s;
            &:hover {
                background: #fff;
                color: $yellow;
                border-color: $yellow;
            }
            @media (max-width:420px) {
                margin: 0 auto;
            }
        }
    }
    a {
        &:hover {
            text-decoration: none;
        }
    }
    .owl {
        &-nav {
            font-size: 24px;
            font-family: 'icomoon' !important;
                  speak: none;
                  font-style: normal;
                  font-weight: normal;
                  font-variant: normal;
                  text-transform: none;
                  line-height: 1;
                  -webkit-font-smoothing: antialiased;
                  -moz-osx-font-smoothing: grayscale;
            & > * {
                position: absolute;
                top: 50%;
                margin-top: -62px;
                &:hover {
                        
                        &:before {
                            color: $yellow;
                        }
                        
                    }
            }
        }
        &-prev {
            left: -90px;
            &:before {
                content: "\e90d";
                display: block;
                color: #64676f;
            }
        }
        &-next {
                right: -117px;
            &:before {
                content: "\e90d";
                display: block;
                transform: rotate(-180deg);
                color: #64676f;
            }
        }
        &-dots {
            text-align: center;
            position: absolute;
            bottom: 10px;
            left: 0;
            right: 0;
            z-index: 999;
            > * {
                display: inline-block;
                vertical-align: middle;
                margin: 0 4.5px;
                width: 8px;
                height: 8px;
                border-radius: 50%;
                background: #6a7081!important;
                transition: all ease 0.4s;
                &.active {
                    box-shadow: 0 0 0 4px #fab915;
                    background: none!important;
                }
            }
        }
    }
    
}
.o_slider {
    .trainers_item {
        width: 100%;
    }
    .owl-item {
        &.active {
            
            filter: grayscale(100%);
            opacity: 0.4;
            @media (max-width:1000px) { 
                filter: grayscale(0%);
                opacity:1;
            }
            & ~ * {
                filter: grayscale(0%);
                opacity: 1;
                
            }
            & + * + * + * + * {
                filter: grayscale(100%);
                opacity: 0.4;
                @media (max-width:600px) { 
                    filter: grayscale(0%)!important;
                    opacity: 1!important;
                }
            }
        }
        &:not(.active) {
            filter: grayscale(100%);
            opacity: 0.4;
        }
    }
    .owl {
        &-nav {
            font-size: 24px;
            font-family: 'icomoon' !important;
                  speak: none;
                  font-style: normal;
                  font-weight: normal;
                  font-variant: normal;
                  text-transform: none;
                  line-height: 1;
                  -webkit-font-smoothing: antialiased;
                  -moz-osx-font-smoothing: grayscale;
            & > * {
                position: absolute;
                &:hover {
                        
                        &:before {
                            color: $yellow;
                        }
                        
                    }
            }
        }
        &-prev {
            left: 30%;
            &:before {
                content: "\e90d";
                display: block;
                color: #64676f;
            }
        }
        &-next {
            right: 30%;
            &:before {
                content: "\e90d";
                display: block;
                transform: rotate(-180deg);
                color: #64676f;
            }
        }
        &-dots {
            text-align: center;
            z-index: 999;
            > * {
                display: inline-block;
                vertical-align: middle;
                margin: 0 4.5px;
                width: 8px;
                height: 8px;
                border-radius: 50%;
                background: #6a7081!important;
                transition: all ease 0.4s;
                &.active {
                    box-shadow: 0 0 0 4px #fab915;
                    background: none!important;
                }
            }
        }
    }
    .owl-stage-outer {
        margin-bottom: 50px;
    }
}
@import "owl/owl.carousel";
@import "owl/owl.theme.default";
