@import "variables";
@import "mixins";

html,
body {
	font-family: 'Ubuntu', sans-serif;
	font-weight: 400;
	font-size: 14px;
	background: #fff;
}
body {
    overflow-x: hidden;
    &.hidden {
        overflow: hidden;
    }
}
.wrapper {
	max-width: 1170px;
	padding: 0 15px;
	margin: 0 auto;
}
a {
	color: #fab915;
	text-decoration: none;
	&:hover {
		text-decoration: underline;
	}
	&.btn {
		font-size: 13px;
		font-weight: bold;
		text-transform: uppercase;
        padding: 20px 27px;
		display: inline-block;
		vertical-align: middle;
		border: 2px solid $yellow;
		text-align: center;
        position: relative;
        &:before {
			content: '';
			display: block;
			background-color: $yellow;
			width:0;
			top: 0;
			bottom: 0;
			position:absolute;
			left:0%;
			transition: all ease 0.5s;
            z-index: 0;
		}
		&:hover {
			color: #000!important;
            text-decoration: none;
			&:before {
				width: 100%;
			}
		}
        span {
            position: relative;
            z-index: 2;
        }
		&.yellow {
			background: $yellow;
			color: #fff;
            i {
                color: #fff;
            }
            &:after,
            &:before {
                content: '';
                display: block;
                background-color: $yellow;
                width:0;
                top: 0;
                position:absolute;
                left:0%;
                transition: all ease 0.5s;
                height: 2px;
            }
            &:after {
                top: auto;
                bottom: 0;
                left: auto;
                right: 0;
            }
            &:hover {
                background: none;
                color: #363a45!important;
                span {
                    color: #fff!important;
                }
                border-color: rgba(255,255,255,0);
                &:after,
                &:before {
                    width: 100%;
                }
            }
		}
		&.black {
			background: none;
			border-color: #1c1c1c;
			color: #1c1c1c;
            position: relative;
            
            &:before {
                content: '';
                display: block;
                background-color: $yellow;
                width:0;
                top: 0;
                bottom: 0;
                position:absolute;
                left:0%;
                transition: all ease 0.5s;
                z-index: 0;
            }
            &:hover {
                color: #000!important;
                &:before {
                    width: 100%;
                }
                span {
                    color: #fff;
                }
            }
		}
		&.white {
			background: none;
			border-color:#fff;
			color: #fff;
            &:hover {
                i {
                    color: #000;
                }
            }
		}
		&.grey {
			background: #ebebeb;
			border-color:#ebebeb;
			color: #5b5b5b;
			i {
				font-size: 25px;
				vertical-align: middle;
				margin-left: 10px;
				color: #afb0b4;
			}
            
		}
        &.blue {
            background: #363a45;
			border-color:#363a45;
			color: #fff;
            &:hover {
                span {
                    color: #fff;
                }
            }
        }
		i {
			vertical-align: text-top;
		}
	}
}
[class^="icon-"], [class*=" icon-"] {
	color: $yellow;
	margin-right: 7px;
	vertical-align: middle;
}
blockquote {
	padding: 14px 0 14px 16px;
	border-left: 1px solid $yellow;
	margin-bottom: 35px;
	font-size: 20px;
	font-weight: 100;
	line-height: 32px;
}
.brake {
	&_layout {
		position: relative;
		overflow: hidden;
		&_img {
			position: absolute;
			top: 0;
			left: 55%;
			bottom: 0;
			right: 0%;
			background-size: cover;
            @media (max-width:1024px) {
               display: none;
            }
		}
        @media (max-width:1024px) {
            .block_lr {
                flex-direction: column;
                & > div {
                    width: 100%;
                }
            }
        }
	}
}
p {
	line-height: 28px;
	color: #5b5b5b;
}
//wtf
.left_border {
	border-left: 2px solid $yellow;
	padding: 7px 20px;
}
h3 {
	font-size: 20px;
	font-weight: 700;
	margin-bottom: 30px
}
ul {
	line-height: 28px;
	list-style: none;
	    padding-left: 17px;
	li {
		margin-bottom: 25px;
		position: relative;
		&:before {
			content: '';
			display: inline-block;
			width: 6px;
			height: 6px;
			background: $yellow;
			margin-left: -16px;
			margin-right: 8px;
			margin-top: 11px;
			vertical-align: top;
		}
	}
}
.center {
    text-align: center;
    .btn {
        min-width:275px;
    }
}
.location {
    position: relative;
}
.mob {
    &_menu {
        display: none;
        font-size: 22px;
        @media (max-width:900px) {
            display: block;
            border: none;
        }
    }
    &_close {
        @media (max-width:900px) {
            display: block;
            position: absolute;
            width: 25px;
            height: 25px;
            border: none!important;
            background: none!important;
            top: 10px;
            right: 10px;
            padding: 0!important;
            cursor: pointer;
            &:after,
            &:before {
                content: '';
                display: block;
                position: absolute;
                left: 50%;
                top: 50%;
                height: 2px;
                background: $yellow;
                width: 25px;
                margin: -1px 0 0 -12.5px;
                transform: rotate(45deg);
            }
            &:after {
                transform: rotate(-45deg);
            }
        }
    }
}

.nav_block {
    @media (max-width:900px) {
        padding: 0;
        padding-top: 45px;
        z-index: 999;
        overflow: auto;
        overflow-x: hidden;
        position: fixed;
                width: 320px;
                left: -320px;
                top: 0;
                bottom: 0;
                background: $dark;
                display: block;
                border-bottom: none;
        transition: left 0.4s ease;
        &.open {
            left: 0px;
        }
    }
}
b {
    font-weight: 700;
}
.bg_grey.brake_layout {
    blockquote {
        & ~ .btn.yellow {
           
            &:hover {
                span {
                    color: #000!important;
                }
            }
        }
    }
}
.hidden {
    &_btn {
        display: none;
        position: absolute;
        top: 0;
        right: 0;
        padding: 12px;
        background: #363a45;
        z-index: 10;
        
        text-align: right;
        @media (max-width:700px) {
            bottom: -15px;
            top: auto;
            width: 100%;
            text-align: center;
            .btn {
                display: inline-block;
                width: auto;
                
            }
        }
        .btn {
            span {
                color: #000;
            }
            & + .btn {
                margin-left: 20px;
            }
        }
    }
}

