a.btn.anim {
	position: relative;
	overflow: hidden;
	transition: all ease 0.5s;
	span {
		position: relative;
		z-index: 2;
	}
	&_one {
		&:before {
			content: '';
			display: block;
			background-color: #fff;
			width:100%;
			height:20px;
			position:absolute;
			left:-100%;
			transform: rotate(45deg);
			transition: all ease 0.5s;
		}
		&:hover {
			background: $yellow;
			color: #000;
			&:before {
				left: 135px;
			}
		}
	}
	&_two {
		&:before {
			content: '';
			display: block;
			background-color: $yellow;
			width:0;
			top: 0;
			bottom: 0;
			position:absolute;
			left:0%;
			transition: all ease 0.5s;
            z-index: 0;
		}
		&:hover {
			color: #000!important;
			&:before {
				width: 100%;
			}
		}
	}
	&_tree {
		&:after,
		&:before {
			content: '';
			display: block;
			background-color: $yellow;
			width:0;
			top: 0;
			position:absolute;
			left:0%;
			transition: all ease 0.5s;
			height: 2px;
		}
		&:after {
			top: auto;
			bottom: 0;
			left: auto;
			right: 0;
		}
		&:hover {
			background: none;
			color: #363a45!important;
            i,
            span {
                color: #1c1c1c!important;
            }
			border-color: rgba(255,255,255,0);
			&:after,
			&:before {
				width: 100%;
			}
		}
	}
	&_four {
		&:before {
			content: '';
			display: block;
			background-color: $yellow;
			width:100%;
			top: 50%;
			position:absolute;
			left:0%;
			right: 0;
			transition: all ease 0.5s;
			height: 0px;
		}
		&:hover {
//			background: none;
			color: #363a45;
//			border-color: rgba(255,255,255,0);
			&:before {
				height: 100%;
				top: 0;
			}
		}
	}
	&_five {
		&:before {
			content: '';
			display: block;
			background-color: $yellow;
			width:0%;
			top: 0;
			bottom: 0;
			position:absolute;
			left:50%;
			transition: all ease 0.5s;
			height: 100%;
		}
		&:hover {
//			background: none;
			color: #363a45;
//			border-color: rgba(255,255,255,0);
			&:before {
				width: 100%;
				left: 0;
			}
		}
	}
	&_six {
		&:before {
			content: '';
			display: block;
			position:absolute;
			top: 0;
			bottom: 0;
			left:0;
			right: 0;
			transition: all ease 2s;
			border: 0px solid $yellow;
		}
		&:hover {
//			background: none;
			color: #363a45;
//			border-color: rgba(255,255,255,0);
			&:before {
				border-width: 500px;
			}
		}
	}
	&_seven {
		&:after,
		&:before {
			content: '';
			display: block;
			position:absolute;
			top: 0;
			bottom: 0;
			left: 50%;
			opacity: 0;
			transition: all ease 0.5s;
			width: 0;
			border-left: 2px solid $yellow;
			border-right: 2px solid $yellow;
		}
		&:hover {
//			background: none;
			color: #363a45;
//			border-color: rgba(255,255,255,0);
			&:before {
				width: calc( 100% + 4px );
				left: -2px;
				opacity: 1;
			}
		}
	}
	
}