.main {
	&_ad {
		background: #efefef;
		&_temp {
			padding: 70px 0;
			font-size: 90px;
			color: #d9d9d9;
			text-align: center;
			text-transform: uppercase;
			font-weight: 500;
            @media (max-width:500px) {
                font-size: 50px;
            }
		}
	}
}
.block {
	padding: 45px 0 50px 0;
	&_head {
		font-size: 40px;
		color: #1c1c1c;
		text-align: center;
		&:after {
			content: '';
			display: block;
			height: 4px;
			width: 20px;
			border-top: 1px solid $yellow;
			border-bottom: 1px solid $yellow;
			margin: 25px auto 55px auto;
		}
		b {
			font-weight: 500;
		}
		&_second {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 50px;
			&:after {
				display: none;
			}
            @media (max-width:500px) {
                display: block;
                h2 {
                    margin-bottom: 15px;
                }
            }
            .btn {
                min-width: 185px;
            }
		}
        &_bottom {
            max-width: 600px;
            margin: 0 auto 30px auto;
            font-weight: 100;
            margin-top: -30px;
        }
	}
	&_cont {
//		border-bottom: 1px solid #ebebeb;
        margin-bottom: 55px;
	}
	&_btn {
		text-align: center;
		a {
			min-width: 270px;
			& + a {
				margin-left: 30px;
                @media (max-width:630px) {
                    margin-left: 0;
                    margin-top: 10px;
                }
			}
		}
	}
	&_lr {
		display: flex;
		> div {
			width: 50%;
		}
		p {
			line-height: 28px;
			margin-bottom: 30px;
		}
		//!!!! REMOVE !!!!!
		.block_head {
			span  {
				> b {
					display: block;
				}
				text-align: left;
			}
		}
        .left {
            img {
                width: calc( 100% - 20px );
            }
        }
	}
	
	
	
	
	&.seo {
		p {
			line-height: 32px;
			color: #5b5b5b;
		}
	}
	
	// BG
	
	&.bg {
		&_blue {
			background: $blue;
			color: #fff;
			&_second {
				background: #474c5b;
				color: #fff;
				.block_head {
					color: #fff;
				}
                a.btn {
                    color: #fff;
                }
			}
			.block_head {
				color: #fff;
			}
            .timetable_btn {
                .btn {
                    color: #fff;
                }
            }
		}
		&_grey {
			background: #f5f5f5;
		}
	}
	
	&.sub {
		background: url(../images/bg/sub.png) no-repeat top center;
		background-size: cover;
            padding: 60px 0 70px;
        .btn.yellow {
            &:hover {
                span {
                    color: #fff!important;
                }
            }
        }
	}
	
}


.rev {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    
	&_block {
		
		border-bottom: 1px solid #ebebeb;
		margin-bottom: 30px;
        @media (max-width:650px) {
            flex-wrap: wrap;
            border-bottom: none;
            margin-bottom: 0; 
        }
		& > * {
			width: 48%;
            @media (max-width:650px) {
                width: 100%;
                border-bottom: 1px solid #ebebeb;
                margin-bottom: 30px;
            }
		}
	}
	&_item {
		padding-bottom: 45px;
		&_name {
			font-weight: bold;
			color: #1c1c1c;
			font-size: 20px;
			margin-bottom: 13px;
            display: block;
            &:hover {
                text-decoration: none;
                color: $yellow;
            }
            a { 
                font-weight: bold;
                color: #1c1c1c;
                font-size: 20px;
                margin-bottom: 13px;
                display: block;
                &:hover {
                    text-decoration: none;
                    color: $yellow;
                }
            }
		}
		&_text {
			line-height: 28px;
			color: #5b5b5b;
//			padding-bottom: 30px;
            padding-bottom: 0px;
            height: calc( 100% - 60px );
		}
		&_info {
			display: flex;
			justify-content: space-between;
			align-items: center;
            padding-right: 40px;
            .date {
                i {
                    font-size: 10px;
                }
            }
		}
	}
}
.city {
	&_dd {
        position: relative;
        padding: 10px 0;
            white-space: nowrap;
		i {
            margin-top: 2px;
			& + span {
				cursor: pointer;
				color: #1c1c1c;
				font-size: 15px;
				font-weight: 500;
				&:after {
					content: '';
					display: inline-block;
					width: 0;
					height: 0;
					border: 3px solid rgba(255,255,255,0);
					border-top-color: #000;
					border-top-width: 5px;
					vertical-align: top;
					margin-top: 7px;
					
					margin-left: 5px;
				}
			}
		}
        &_block {
            position: absolute;
            background: #f8f8f8;
            box-shadow: 0 0 1px rgba(0,0,0,0.4);
            left: 0;
            top: 100%;
            display: none;
                min-width: 100%;
                z-index: 5;
            a {
                text-decoration: none;
                color: #000;
                padding: 5px 10px;
                display: block;
                white-space: nowrap;
                &:hover {
                    background: $yellow;
                    color: #fff;
                }
            }
        }
	}
}
.date {
	font-size: 15px;
	font-weight: 500;
	color: #1c1c1c;
    white-space: nowrap;
}
.timetable {
	&_item {
		border-top: 1px solid rgba(235, 235, 235, 0.1);
		padding: 24px 0 35px;
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		&:last-child {
			border-bottom: 1px solid rgba(235, 235, 235, 0.1);
		}
        @media (max-width:850px) {
            flex-wrap: wrap;   
                justify-content: flex-start;
        }
        @media (max-width:630px) {
            display: block;
        }
		& > div {
			min-width: 185px;
			display: flex;
			justify-content: flex-start;
			align-items: center;
            @media (max-width:630px) {
                justify-content: center;   
            }
			& + div {
				border-left: 1px solid rgba(235, 235, 235, 0.1); 
				padding-left: 30px;
				flex-direction: column;
				
				justify-content: center;
				align-items: flex-start;
                @media (max-width:630px) {
                    padding-left: 0;
                    text-align: center!important;
                    display: block!important;
                    border: none;
                    padding: 15px 0;
                }
			}
			&.timetable_info {
				min-width: 200px;
			}
			&.timetable_date + .timetable_info {
				min-width: 230px;
			} 
		}
	}
	&_date {
		font-size: 20px;
		display: flex;
            font-weight: 300;
		div {
			&:first-child {
				color: $yellow;
				font-size: 58px;
				line-height: 46px;
				margin-right: 17px;
			}
		}
        @media (max-width:850px) {
            order: 1;   
            min-width: 33%!important;
        }
	}
	.btn {
		padding: 20px 26px;
        
	}
	&_info {
        .city_dd {
            padding: 0;
        }
		& > div {
			margin-bottom: 15px; 
		}
        @media (max-width:850px) {
            order: 3;  
            margin-top: 15px;
            min-width: 33%!important;
        }
	}
	&_text {
		line-height: 30px;
            min-width: 335px!important;
        @media (max-width:1080px) {
            display: none!important;
        }
        @media (max-width:850px) {
            display: flex!important;
            order: 2;
            min-width: 66%!important;
                flex-basis: 66%;
        }
        
	}
    &_btn {
        @media (max-width:850px) {
            min-width: 33%!important;
            order: 4;
            justify-content: flex-end!important;
        }
    }
	&.white {
		.timetable_date div:first-child {
			color: #1c1c1c;
		}
		a {
			font-weight: bold;
			color: #1c1c1c;
		}
		.timetable_item > div + div {
			border-color: #ebebeb;
		}
		.timetable_item {
			background: #f4f4f4;
			padding-left: 15px;
			& + .timetable_item {
				margin-top: 20px;
			}
		}
	}
}
.sub {
	color: #fff;
	font-size: 35px;
	font-weight: 100;
    @media (max-width:600px) {
        font-size: 28px;
    }
    @media (max-width:500px) {
        font-size: 22px;
    }
    
	b {
		font-weight: 500;
	}
	i {
		margin-right: 25px;
        font-size: 43px;
	}
