        font-size: 20px;
        font-weight: 100;
    }
}
.news {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	padding-bottom: 30px;
	border-bottom: 1px solid #ebebeb;
	margin-bottom: 30px;
	&_item {
		width: 30%;
        & + & {
            margin-left: 3%;
        }
        @media (max-width:768px) {
            width: calc( 50% - 10px );
            margin-bottom: 25px;
        }
        @media (max-width:600px) {
            width: 100%;
            
            & + * {
                border-top: 1px solid #ebebeb;
                padding-top: 25px;
            }
        }
		&_name {
			font-size: 20px;
			font-weight: 500;
			color: #1c1c1c;
			display: block;
			margin-bottom: 30px;
            &:hover {
                text-decoration: none;
                color: $yellow;
            }
		}
		&_img {
			margin-bottom: 30px;
			display: block;
			img {
				width: 100%;
			}
		}
		&_info {
			margin-bottom: 25px;
            font-weight: 500;
            color: #1c1c1c;
			.date {
				margin-right: 30px;
			}
		}
		p {
			color: #5b5b5b;
			line-height: 28px;
			font-size: 14px;
			padding-bottom: 20px;
		}
		&_more {
			color: #1c1c1c;
			font-size: 13px;
			font-weight: 600;
			text-transform: uppercase;
			i {
				color: #000;
				transform: rotate(180deg);
				display: inline-block;
				margin-left: 7px;
				margin-top: -3px;
			}
            &:hover {
                color: $yellow;
                text-decoration: none;
                i {
                    color: $yellow;
                }
            }
		}
	}
    &_top {
        background: #363a45;
        &_block {
            display: flex;
            justify-content: center;
            align-items: center;
            color: #fff;
            padding: 25px 0;
            @media (max-width:500px) {
                display: block;
                text-align: center;
            }
            > * {
                margin: 0 15px;
                color: #fff;
                font-weight:100;
                &.views {
                    font-weight: 500;
                }
                @media (max-width:500px) {
                    margin: 10px 15px;
                }
            }
        }
    }
}


