        &:not(.location) {
            font-size: 20px;
            font-weight: 500;
            padding: 25px;
            white-space: nowrap;
                width: 33%;
            @media (max-width:920px) {
                font-size: 16px;
            }
            @media (max-width:820px) {
                font-size: 20px;
                text-align: center;
                width: 100%;
            }
        }
        &.location {
            width: 33%;
            padding: 18px 0;
            @media (max-width:820px) {
                width: 33%;   
            } 
            @media (max-width:620px) {
                width: 100%;   
                padding-left: 10px;
                padding-right: 10px;
            }
        }
    }
    & > a {
        flex-basis: 190px;
        width: 190px;
        min-width: 190px;
        text-align: center;
        padding: 29px 0; 
        font-weight: 500;
        color: #1c1c1c;
        @media (max-width:820px) {
            min-width: 33%;   
        }
        @media (max-width:620px) {
            min-width: 50%;   
        }
        @media (max-width:420px) {
            min-width: 100%;   
        }
        &.active {
            background: #efefef;
            font-weight: 500;
        }
        &:hover {
            text-decoration: none;
        }
    }
    .city_dd {
        width: 100%;
        i {
            display: inline-block;
            float: left;
            & + span {
                width: calc( 100% - 32px );
                display: inline-block;
                display: flex;
                justify-content: space-between;
            }
        }
    }
}
