footer {
	background: $blue;
	nav {
		background: $blue;
		margin-bottom: 33px;
		a {
            padding: 39px 0 38px 0;

			&:hover,
			&.current {
				border-bottom-color: rgba(255,255,255,0);
				color: $yellow;
			}
		}
		.wrapper {
			border-bottom: 1px solid #4a4e58;
		}
		& + .wrapper {
			display: flex;
			justify-content: space-between;
			border-bottom: 1px solid #4a4e58;
			margin-bottom: 25px;
            @media (max-width:710px) {
                display: block;
            }
		}
	}
	a {
		color: #fff;
	}
	.left_a {
		display: block;
        margin-bottom: 33px;
        font-weight: 100;
        padding-left: 10px;
		&:hover {
			text-decoration: none;
			color: $yellow;
		}
	}
	
}
.footer {
	&_head {
		color: #fff;
		font-size: 20px;
		text-transform: uppercase;
		margin-bottom: 30px;
	}
	&_btn {
		display: flex;
		justify-content: space-between;
        padding-bottom: 52px;
        a {
            @media (max-width:1024px) and (min-width:710px) {
                padding: 15px;
                font-size: 11px;
            }
            @media (max-width:550px) {
                width: 100%; 
                
            }
            & + a {
                margin-left: 10px;
                @media (max-width:550px) {
                    margin-left: 0; 
                    margin-top: 10px;
                }
            }
        }
        @media (max-width:550px) {
            display: block;
        }
	}
	&_cont {
		.footer_head {
			margin-bottom: 45px;
                padding-top: 11px;
		}
	}
	&_right {
		width: calc( 100% - 400px ); 
        @media (max-width:880px ) {
            width: calc( 100% - 390px);
        }
        @media (max-width:710px) {
            width: 100%;   
        }
	}
	&_sub {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		margin-bottom: 35px;
        & > form {
            display: flex;
            justify-content: space-between;
            align-items: stretch;
            width: 100%;
        }
        @media (max-width:1050px) and (min-width:710px) {
            flex-direction: column;
        }
        @media (max-width:710px) {
            flex-direction: row;
        }
        @media (max-width:710px) {
            display: block;
            & > * {
                width: 100%;
            }
        }
        
		[type="submit"] {
			padding: 28px;
			background: #565c6d;
			border: none;
			color: #fff;
			font-size: 13px;
			font-weight: bold;
			text-transform: uppercase;
			white-space: nowrap;
            &:hover {
                color: $yellow;
            }
            @media (max-width:500px) {
                font-size: 10px;
                padding-left: 0;
                padding-right: 0;
                text-align: center;
            }
		}
		[type="text"] {
			width: 100%;
            border: none;
            outline: none;
            background: #474c5b url(../images/in.png) no-repeat 22px 28px;
            color: #fff;
            padding: 25px 28px 27px 49px;
			@include plhl {
				color: #fff;
			}
            @media (max-width:1050px) and (min-width:800px) {
                
            }
		}
	}
	&_social {
		color: #fff;
		font-size: 15px;
		font-weight: 100;
        margin-bottom: 15px;
		&.share {
			color: #5b5b5b;
			margin-bottom: 35px;
			a {
				i {
					color: #afb0b4;
				}
				&:hover {
					i {
						color: $yellow;
					}
				}
			}
		}
		a {
			font-size: 20px;
			i {
				color: #7f8188;
			}
			&:hover {
				
				text-decoration: none;
				i {
					color: #fff;
				}
			}
			.icon-vk-social {
				font-size: 30px;
			}
			.icon-youtube-social {
				font-size: 24px;
			}
			& + a {
				margin-left: 25px;
                @media (max-width:550px) {
                   margin-left: 15px; 
                }
			}
		}
		div {
			padding-top: 20px;
		}
	}
	&_bottom {
		p {
			color: #fff; 
			line-height: 28px;
			margin-bottom: 30px;
			font-weight: 100;
		}
	}
	&_copy {
		display: flex;
		justify-content: space-between;
		align-items: center;
		color: rgba(255,255,255,.26);
		padding-bottom: 40px;
		a {
			color: $yellow;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
    &_nav {
        @media (max-width:900px) {
            display: none;
        }
        & + a {
            &:hover {
                color: #fff;
            }
        }
    }
}